.navbar{
    position: fixed;
    top: 0;
    width: 100%;
    padding: 20px;
    display: flex;
    height: 30px;
    justify-content: space-between;
    z-index: 1;

    transition-timing-function: ease-in;
    transition: all 0.5s;
}
.navbar_black{
    background-color: #111;
}
.navbar_logo {
    position: fixed;
    left: 20px;
    width: 80px;
    object-fit: contain;
}

.navbar_menu {
    position: fixed;
    right: 20px;
    width: 30px;
    object-fit: contain;
}